import React, {Component} from 'react';
import {Bar, Line, Bubble} from 'react-chartjs-2';

class RSIChart extends Component{
  constructor(props){
    super(props);
    this.state = {
      data:props.crData
      //chartData:{}

    }
  }
  render(){
    let PSARData =  {
      labels: this.props.crData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'',
        yAxisID: '1',
        data:   this.props.crData.map(function(market){
                  if (market.actionValue===0.0) {return (null)} else {return(market.actionValue)};
                }),
                pointBackgroundColor: this.props.crData.map(function(market){
                  if (market.actionValue===0.0) {return (null)} else {return(market.actionColour)};
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 5,
        fill: false
              },
              {
        scaleStartValue:'5000',
        label:'PSAR 0.02/0.2',
        yAxisID: '1',
        data:   this.props.crData.map(function(market){
          if (market.psar===0.0) {return (null)} else {return(market.psar)};
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 1,
        showLine: false,
        fill: false
              },
              {
                scaleStartValue:5000,
                label:'RSI 14',
                yAxisID: '2',
                data:   this.props.crData.map(function(market){
                          if (market.rsi===0.0) {return (null)} else {return(market.rsi)};
                        }),
                        type: 'line',
                        color: 'rgb(17,24,41)',
                        drawTicks: true,
                backgroundColor: 'darkorange',
                border: false,
                borderColor:  'darkorange',
                borderWidth: 1,
                pointRadius: 0,
                fill: false
                      },
              {
                scaleStartValue:5000,
                label:'BB Upper',
                yAxisID: '1',
                data:   this.props.crData.map(function(market){
                          if (market.bbUpper===0.0) {return (null)} else {return(market.bbUpper)};
                        }),
                        type: 'line',
                        color: 'rgb(121,203,248)',
                        drawTicks: true,
                backgroundColor: 'rgb(121,203,248)',
                border: false,
                borderColor:  'rgb(121,203,248)',
                borderWidth: 1,
                pointRadius: 0,
                fill: false
                      },
                      {
                        scaleStartValue:5000,
                        label:'BB Middle',
                        yAxisID: '1',
                        data:   this.props.crData.map(function(market){
                                  if (market.bbMiddle===0.0) {return (null)} else {return(market.bbMiddle)};
                                }),
                                type: 'line',
                                color: 'rgb(248,203,121)',
                                drawTicks: true,
                        backgroundColor: 'rgb(248,203,121)',
                        border: false,
                        borderColor:  'rgb(248,203,121)',
                        borderWidth: 1,
                        pointRadius: 0,
                        fill: false
                              },
                              {
                                scaleStartValue:5000,
                                label:'BB Lower',
                                yAxisID: '1',
                                data:   this.props.crData.map(function(market){
                                          if (market.bbLower===0.0) {return (null)} else {return(market.bbLower)};
                                        }),
                                        type: 'line',
                                        color: 'rgb(248,121,121)',
                                        drawTicks: true,
                                backgroundColor: 'rgb(248,121,121)',
                                border: false,
                                borderColor:  'rgb(248,121,121)',
                                borderWidth: 1,
                                pointRadius: 0,
                                fill: false
                                      },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.crData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      },
      {
        label:'MACD F:12 S:24',
        yAxisID: '3',
        data:   this.props.crData.map(function(market){
          if (market.macd===0.0) {return (null)} else {return(market.macd)};
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
                borderWidth: 1,
        backgroundColor: 'rgb(121,203,248)',
        borderColor:  'rgb(121,203,248)',
        pointRadius: 0,
        fill: false
              },
              {
                label:'MACD Signal Line',
                yAxisID: '3',
                data:   this.props.crData.map(function(market){
                  if (market.macdSignal===0.0) {return (null)} else {return(market.macdSignal)};
                        }),
                        type: 'line',
                        color: 'rgb(17,24,41)',
                        drawTicks: true,
                        borderWidth: 1,
                backgroundColor: 'rgb(248,203,121)',
                borderColor:  'rgb(248,203,121)',
                pointRadius: 0,
                fill: false
                      },
                      {
        label:'MACD Histogram',
        yAxisID: '3',
        data:   this.props.crData.map(function(market){
          if (market.macdHistogram===0.0) {return (null)} else {return(market.macdHistogram)};
                        }),
                        type: 'line',
                        pointRadius: 0,
                        borderWidth: 1,
                        drawTicks: true,
        backgroundColor: 'lightgreay'//'rgb(205,94,151)'
      },
      {
        scaleStartValue:5000,
        label:'Fast',
        yAxisID: '1',
        data:   this.props.crData.map(function(market){
                  if (market.fastEMA===0.0) {return (null)} else {return(market.fastEMA)};
                }),
                type: 'line',
                color: 'rgb(121,203,248)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 1,
        pointRadius: 0,
        fill: false
              },
              {
                scaleStartValue:5000,
                label:'Medium',
                yAxisID: '1',
                data:   this.props.crData.map(function(market){
                          if (market.medEMA===0.0) {return (null)} else {return(market.medEMA)};
                        }),
                        type: 'line',
                        color: 'rgb(248,203,121)',
                        drawTicks: true,
                backgroundColor: 'rgb(248,203,121)',
                border: false,
                borderColor:  'rgb(248,203,121)',
                borderWidth: 1,
                pointRadius: 0,
                fill: false
                      },
                      {
                        scaleStartValue:5000,
                        label:'Slow',
                        yAxisID: '1',
                        data:   this.props.crData.map(function(market){
                                  if (market.slowEMA===0.0) {return (null)} else {return(market.slowEMA)};
                                }),
                                type: 'line',
                                color: 'rgb(248,121,121)',
                                drawTicks: true,
                        backgroundColor: 'rgb(248,121,121)',
                        border: false,
                        borderColor:  'rgb(248,121,121)',
                        borderWidth: 1,
                        pointRadius: 0,
                        fill: false
                              },
                              {
                                scaleStartValue:5000,
                                label:'Fast SMA',
                                yAxisID: '1',
                                data:   this.props.crData.map(function(market){
                                          if (market.fastSMA===0.0) {return (null)} else {return(market.fastSMA)};
                                        }),
                                        type: 'line',
                                        color: 'rgb(121,203,248)',
                                        drawTicks: true,
                                backgroundColor: 'rgb(121,203,248)',
                                border: false,
                                borderColor:  'rgb(121,203,248)',
                                borderWidth: 1,
                                pointRadius: 0,
                                fill: false
                                      },
                                              {
                                                scaleStartValue:5000,
                                                label:'Slow SMA',
                                                yAxisID: '1',
                                                data:   this.props.crData.map(function(market){
                                                          if (market.slowSMA===0.0) {return (null)} else {return(market.slowSMA)};
                                                        }),
                                                        type: 'line',
                                                        color: 'rgb(248,121,121)',
                                                        drawTicks: true,
                                                backgroundColor: 'rgb(248,121,121)',
                                                border: false,
                                                borderColor:  'rgb(248,121,121)',
                                                borderWidth: 1,
                                                pointRadius: 0,
                                                fill: false
                                                      },
                                                      {
                                                        scaleStartValue:5000,
                                                        label:'Accumulation Distribution',
                                                        yAxisID: '1',
                                                        data:   this.props.crData.map(function(market){
                                                                  if (market.accumDist===0.0) {return (null)} else {return(market.accumDist)};
                                                                }),
                                                                type: 'line',
                                                                color: 'rgb(248,121,121)',
                                                                drawTicks: true,
                                                        backgroundColor: 'rgb(248,121,121)',
                                                        border: false,
                                                        borderColor:  'rgb(248,121,121)',
                                                        borderWidth: 1,
                                                        pointRadius: 0,
                                                        fill: false
                                                              },
                                                              {
                                                                scaleStartValue:5000,
                                                                label:'Average True Range',
                                                                yAxisID: '1',
                                                                data:   this.props.crData.map(function(market){
                                                                          if (market.atr===0.0) {return (null)} else {return(market.atr)};
                                                                        }),
                                                                        type: 'line',
                                                                        color: 'rgb(248,121,121)',
                                                                        drawTicks: true,
                                                                backgroundColor: 'rgb(248,121,121)',
                                                                border: false,
                                                                borderColor:  'rgb(248,121,121)',
                                                                borderWidth: 1,
                                                                pointRadius: 0,
                                                                fill: false
                                                                      }
    ]
    }
    



    return (
      <div className="rsichart">
      <Line height="200"
        data={PSARData}
        options={{
          responsive: true,
          //maintainAspectRatio: false,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               //min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '0',
               max: '100',
               position: 'right',
               ticks: {
                 min: 0,
                 max: 100
               }
             }, {
              id: '3',
              scaleStartValue:5000,
              type: 'linear',
              //min: '0',
              //max: '100',
              position: 'right'
            }]
           }

        }}
        />
       {/* <Line height="175"
        data={BBData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }
            //  , {
            //    id: '2',
            //    scaleStartValue:5000,
            //    type: 'linear',
            //    min: '2018-06-12T16:15:27.077',
            //    position: 'right'
            //  }
            ]
           }

        }}
        />
       {/* <Line height="175"
        data={SMAData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }
            //  , {
            //    id: '2',
            //    scaleStartValue:5000,
            //    type: 'linear',
            //    min: '2018-06-12T16:15:27.077',
            //    position: 'right'
            //  }
            ]
           }

        }}
        />
        <Line height="175"
        data={EMAData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }
            //  , {
            //    id: '2',
            //    scaleStartValue:5000,
            //    type: 'linear',
            //    min: '2018-06-12T16:15:27.077',
            //    position: 'right'
            //  }
            ]
           }

        }}
        />*/}
      {/* <Line height="175"
        data={priceData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }
            ]
           }

        }}
        />   */}

      </div>
    )
  }
}
export default RSIChart



