import React, {Component} from 'react';
import {Bar, Line, Bubble} from 'react-chartjs-2';

class Chart extends Component{
  constructor(props){
    super(props);
    this.state = {
      data:props.cData
      //chartData:{}

    }
  }
  render(){

    let AccumData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'Accumulation Distribution',
        yAxisID: '2',
        data:   this.props.cData.map(function(market){
                  return market.accDist;
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 0,
        showLine: true,
        fill: false
              },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      }]
    }

    let ATRData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'Average True Range',
        yAxisID: '2',
        data:   this.props.cData.map(function(market){
                  return market.atr;
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 0,
        showLine: true,
        fill: false
              },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      }]
    }

    let MFIData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'Money Flow Index',
        yAxisID: '2',
        data:   this.props.cData.map(function(market){
                  return market.mfi;
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 0,
        showLine: true,
        fill: false
              },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      }]
    }

    let WilliamsRData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'WilliamsR',
        yAxisID: '2',
        data:   this.props.cData.map(function(market){
                  return market.williamsR;
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 0,
        pointRadius: 0,
        showLine: true,
        fill: false
              },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      }]
    }

    let StochasticData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        scaleStartValue:'5000',
        label:'Stochastic K Line',
        yAxisID: '2',
        data:   this.props.cData.map(function(market){
                  return market.stochasticK ;
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
        backgroundColor: 'rgb(121,203,248)',
        border: false,
        borderColor:  'rgb(121,203,248)',
        borderWidth: 1,
        pointRadius: 0,
        fill: false
              },
              {
                scaleStartValue:5000,
                label:'Stochastic D Line',
                yAxisID: '2',
                data:   this.props.cData.map(function(market){
                          return market.stochasticD;
                        }),
                        type: 'line',
                        color: 'rgb(17,24,41)',
                        drawTicks: true,
                backgroundColor: 'rgb(248,203,121)',
                border: false,
                borderColor:  'rgb(248,203,121)',
                borderWidth: 1,
                pointRadius: 0,
                fill: false
                      },
                      {
        label:'USDT-BTC Price',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                          return market.last;
                        }),
                        type: 'line',
                        border: false,
                        pointRadius: 0,
                        borderColor: 'darkgrey',
                        borderWidth: 2,
                        fill: false,
                        drawTicks: true,
        backgroundColor: 'darkgrey'//'rgb(205,94,151)'
      }]
    }

    let PSARData =  {
        labels: this.props.cData.map(function(market){
                  return market.timeStamp;
                }),
        datasets:[{
          scaleStartValue:'5000',
          label:'PSAR 0.02/0.2',
          yAxisID: '1',
          data:   this.props.cData.map(function(market){
                    return market.psar;
                  }),
                  type: 'line',
                  color: 'rgb(17,24,41)',
                  drawTicks: true,
          backgroundColor: 'rgb(121,203,248)',
          border: false,
          borderColor:  'rgb(121,203,248)',
          borderWidth: 0,
          pointRadius: 1,
          showLine: false,
          fill: false
                },
                        {
          label:'USDT-BTC Price',
          yAxisID: '1',
          data:   this.props.cData.map(function(market){
                            return market.last;
                          }),
                          type: 'line',
                          border: false,
                          pointRadius: 0,
                          borderColor: 'darkgrey',
                          borderWidth: 2,
                          fill: false,
                          drawTicks: true,
          backgroundColor: 'darkgrey'//'rgb(205,94,151)'
        }]
      }

let chartData =  {
    labels: this.props.cData.map(function(market){
              return market.timeStamp;
            }),
    datasets:[{
      label:'MACD F:12 S:24',
      yAxisID: '1',
      data:   this.props.cData.map(function(market){
                return Math.round(market.macd);
              }),
              type: 'line',
              color: 'rgb(17,24,41)',
              drawTicks: true,
              borderWidth: 1,
      backgroundColor: 'rgb(121,203,248)',
      borderColor:  'rgb(121,203,248)',
      pointRadius: 0,
      fill: false
            },
            {
              label:'MACD Signal Line',
              yAxisID: '1',
              data:   this.props.cData.map(function(market){
                        return Math.round(market.macdSignal);
                      }),
                      type: 'line',
                      color: 'rgb(17,24,41)',
                      drawTicks: true,
                      borderWidth: 1,
              backgroundColor: 'rgb(248,203,121)',
              borderColor:  'rgb(248,203,121)',
              pointRadius: 0,
              fill: false
                    },
                    {
      label:'MACD Histogram',
      yAxisID: '1',
      data:   this.props.cData.map(function(market){
                        return market.macdHistogram;
                      }),
                      type: 'line',
                      pointRadius: 0,
                      borderWidth: 1,
                      drawTicks: true,
      backgroundColor: 'lightgreay'//'rgb(205,94,151)'
    }]
  }



  let rsiData =  {
      labels: this.props.cData.map(function(market){
                return market.timeStamp;
              }),
      datasets:[{
        label:'RSI 14',
        yAxisID: '1',
        data:   this.props.cData.map(function(market){
                  return(market.rsi);
                }),
                type: 'line',
                color: 'rgb(17,24,41)',
                drawTicks: true,
                borderWidth: 1,
        backgroundColor: 'rgb(121,203,248)',
        borderColor:  'rgb(121,203,248)',
        pointRadius: 0,
        fill: false
              }]
    }


    let priceData =  {
        labels: this.props.cData.map(function(market){
                  return market.timeStamp;
                }),
        datasets:[{
          scaleStartValue:'5000',
          label:'SMA 20',
          yAxisID: '2',
          data:   this.props.cData.map(function(market){
                    if (market.sma===0.0) {return (6500)} else {return(market.sma)};
                  }),
                  type: 'line',
                  color: 'rgb(17,24,41)',
                  drawTicks: true,
          backgroundColor: 'rgb(121,203,248)',
          border: false,
          borderColor:  'rgb(121,203,248)',
          borderWidth: 1,
          pointRadius: 0,
          fill: false
                },
                {
                  scaleStartValue:5000,
                  label:'EMA 10',
                  yAxisID: '2',
                  data:   this.props.cData.map(function(market){
                            if (market.ema===0.0) {return (6500)} else {return(market.ema)};
                          }),
                          type: 'line',
                          color: 'rgb(17,24,41)',
                          drawTicks: true,
                  backgroundColor: 'rgb(121,203,248)',
                  border: false,
                  borderColor:  'rgb(248,203,121)',
                  borderWidth: 1,
                  pointRadius: 0,
                  fill: false
                        },
                        {
          label:'USDT-BTC Price',
          yAxisID: '1',
          data:   this.props.cData.map(function(market){
                            return market.last;
                          }),
                          type: 'line',
                          border: false,
                          pointRadius: 0,
                          borderColor: 'darkgrey',
                          borderWidth: 2,
                          fill: false,
                          drawTicks: true,
          backgroundColor: 'darkgrey'//'rgb(205,94,151)'
        }]
      }

      let bbData =  {
          labels: this.props.cData.map(function(market){
                    return market.timeStamp;
                  }),
          datasets:[{
            scaleStartValue:'5000',
            label:'Upper Band',
            yAxisID: '1',
            data:   this.props.cData.map(function(market){
                      if (market.sma===0.0) {return (null)} else {return(market.bbUpper)};
                    }),
                    type: 'line',
                    color: 'rgb(17,24,41)',
                    drawTicks: true,
            backgroundColor: 'rgb(121,203,248)',
            border: false,
            borderColor:  'rgb(121,203,248)',
            borderWidth: 1,
            pointRadius: 0,
            fill: false
                  },
                  {
                    scaleStartValue:5000,
                    label:'Middle Band',
                    yAxisID: '1',
                    data:   this.props.cData.map(function(market){
                              if (market.ema===0.0) {return (null)} else {return(market.bbMiddle)};
                            }),
                            type: 'line',
                            color: 'rgb(17,24,41)',
                            drawTicks: true,
                    backgroundColor: 'rgb(248,203,121)',
                    border: false,
                    borderColor:  'rgb(248,203,121)',
                    borderWidth: 1,
                    pointRadius: 0,
                    fill: false
                          },{
                            scaleStartValue:5000,
                            label:'Lower Band',
                            yAxisID: '1',
                            data:   this.props.cData.map(function(market){
                                      if (market.ema===0.0) {return (null)} else {return(market.bbLower)};
                                    }),
                                    type: 'line',
                                    color: 'rgb(17,24,41)',
                                    drawTicks: true,
                            backgroundColor: 'rgb(248,121,121)',
                            border: false,
                            borderColor:  'rgb(248,121,121)',
                            borderWidth: 1,
                            pointRadius: 0,
                            fill: false
                                  },
                          {
            label:'USDT-BTC Price',
            yAxisID: '1',
            data:   this.props.cData.map(function(market){
                              return market.last;
                            }),
                            type: 'line',
                            border: false,
                            pointRadius: 0,
                            borderColor: 'darkgrey',
                            borderWidth: 2,
                            fill: false,
                            drawTicks: true,
            backgroundColor: 'grey'//'rgb(205,94,151)'
          }]
        }



    return (
      <div className="chart">
      <Line height="75"
        data={PSARData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
        data={AccumData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
        data={ATRData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
        data={MFIData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
        data={WilliamsRData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
        data={StochasticData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
      <Line height="75"
        data={priceData}
        options={{
          responsive: true,
          maintainAspectRatio: true,
           scales:{
             xAxes: [{
               // id: '3',
               // ticks: {
               // min: '2018-06-12T16:15:27.077'}
             }],
             xAxes: [{
               display:  true
             }],
             yAxes: [{
               id: '1',
               type: 'linear',
               position: 'left',
               min: '2018-06-12T18:15:27.077',
               ticks: {
               beginAtZero: false
             }
             }, {
               id: '2',
               scaleStartValue:5000,
               type: 'linear',
               min: '2018-06-12T16:15:27.077',
               position: 'right'
             }]
           }

        }}
        />
        <Line height="75"
          data={bbData}
          options={{
            responsive: true,
            maintainAspectRatio: true,
             scales:{
               xAxes: [{
                 // id: '3',
                 // ticks: {
                 // min: '2018-06-12T16:15:27.077'}
               }],
               xAxes: [{
                 display: true
               }],
               yAxes: [{
                 id: '1',
                 type: 'linear',
                 position: 'left',
                 min: '2018-06-12T18:15:27.077',
                 ticks: {
                 beginAtZero: false
               }
               }
              //  , {
              //    id: '2',
              //    scaleStartValue:5000,
              //    type: 'linear',
              //    min: '2018-06-12T16:15:27.077',
              //    position: 'right'
              //  }
              ]
             }

          }}
          />
        <Line height="50"
          data={chartData}
          options={{
            responsive: true,
            maintainAspectRatio: true,
             scales:{
               xAxes: [{
                 // id: '3',
                 // max: '10',
                 // ticks: {
                 // min: '2018-06-12T16:15:27.077'}
               }],
               xAxes: [{
                 display: true
               }],
               yAxes: [{
                 id: '1',
                 type: 'linear',
                 position: 'left',
                 max: '10'
               }
               // ,{
               //   id: '2',
               //   type: 'linear',
               //   position: 'right'
               // }
             ]
             }

          }}
          />
        <Line height="50"
          data={rsiData}
          options={{
            responsive: true,
            maintainAspectRatio: true,
             scales:{
               xAxes: [{
                 display: true
               }],
               yAxes: [{
                 id: '1',
                 type: 'linear',
                 position: 'left'
               }]
             }

          }}
          />
      </div>
    )
  }
}
export default Chart



